<template>
  <b-container fluid>
    <b-row>
      <b-col md="3">
        <iq-card class="calender-small">
          <template v-slot:body>
            <flat-pickr :config="config" :value="Date.now()" v-model="date" @on-change="goToDate(date)" class="d-none" />
          </template>
        </iq-card>
        <iq-card>
          <template v-slot:headerTitle>
            <h4 class="card-title ">Doctors</h4>
          </template>
          <template v-slot:headerAction>
            <div class="iq-search-bar">
              <form action="#">
                <input id="search" v-model="searchText" type="text" class="search-input"
                  placeholder="Type here to search...">
              </form>
            </div>
          </template>

          <b-form-group>
            <template #label v-slot:body>
              <ul>
                <b-form-checkbox class="m-0 p-0 job-classification" v-model="allSelected" :indeterminate="indeterminate"
                  aria-describedby="fullName" aria-controls="fullName" @change="toggleAll">
                  {{ allSelected ? 'Un-select All' : 'Select All' }}
                </b-form-checkbox>
                <b-form-group v-slot="{ ariaDescribedby }">
                  <b-checkbox v-for="staff in filteredList" v-model="selected" class="custom-checkbox-color-check"
                    @change="toggleEventFilter" :key="staff.fullName" :value="staff" :aria-describedby="ariaDescribedby"
                    stacked>
                    <h6><b-badge variant="0" :style="'color: white; opacity: 0.6; background-color: ' + staff.color">{{
                      staff.fullName }}</b-badge></h6>
                  </b-checkbox>
                </b-form-group>
              </ul>

            </template>
          </b-form-group>
        </iq-card>
        <iq-card>
          <template v-slot:headerTitle>
            <h4 class="card-title">Today's Schedule</h4>
          </template>
          <template v-slot:body>
            <ul class="m-0 p-0 today-schedule">
              <li class="d-flex" v-for="event in getDayEvents()" :key="event.id">
                <div class="schedule-icon"><i class="ri-checkbox-blank-circle-fill text-primary"
                    :style="`color: ${event.backgroundColor} !important`" /></div>
                <div class="schedule-text"> <span>{{ event.title }}</span>
                  <span>{{ getPeriodConsult(event) }}</span>
                </div>
              </li>
            </ul>
          </template>
        </iq-card>
      </b-col>
      <b-col md="9">
        <iq-card>
          <template v-slot:headerTitle>
            <h4 class="card-title">Calendar</h4>
          </template>
          <template v-slot:headerAction>
            <b-button @click="showModal()" variant="primary"><i class="ri-add-line mr-2"></i>Book Appointment</b-button>
            <CalendarForm :onDateClick="onDateClick" @onSaveEvent="onSaveEvent($event)" />
          </template>
          <template v-slot:body>
            <FullCalendar @onDateClickEvent="onDateClickEvent($event)" :event="event" />
          </template>
        </iq-card>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { xray } from '../../../config/pluginInit'
import CalendarForm from './form/CalendarForm.vue'
import usersService from '../../../services/user'

export default {
  name: 'Calendar',
  components: {
    CalendarForm
  },
  data () {
    return {
      config: {
        inline: true,
      },
      showForm: false,
      onDateClick: null,
      date: null,
      selected: [],
      // staffs: [
      //   { id: 1, name: 'João Araújo', color: 'primary' },
      //   { id: 2, name: 'Alan Araújo', color: 'info' },
      //   { id: 3, name: 'Sara Araújo', color: 'warning' },
      //   { id: 4, name: 'maria Araújo', color: 'primary' },
      //   { id: 5, name: 'teste Araújo', color: 'info' },
      //   { id: 6, name: 'function Araújo', color: 'warning' },
      //   { id: 7, name: 'fulano Araújo', color: 'primary' },
      //   { id: 8, name: 'sico Araújo', color: 'info' },
      //   { id: 9, name: 'pelé Araújo', color: 'warning' },
      // ],
      allSelected: true,
      indeterminate: false,
      searchText: '',
      event: null
    }
  },
  created () {

  },
  mounted () {
    xray.index()
    this.getAllStaffs()
    this.showEvents(null, true)
  },
  watch: {
    selected (newValue, oldValue) {
      if (newValue.length === 0) {
        this.indeterminate = false
        this.allSelected = false
      } else if (newValue.length === this.filteredList.length) {
        this.indeterminate = false
        this.allSelected = true
      } else {
        this.indeterminate = true
        this.allSelected = false
      }
    }
  },
  computed: {
    ...mapGetters({
      events: 'Calendar/events',
      clinicState: 'Clinic/clinicState',
    }),
    filteredList () {
      return this.staffs.length > 0 ?
        this.staffs.filter(item => {
          return item.fullName.normalize("NFD").replace(/\p{Diacritic}/gu, "").toLowerCase().includes(this.searchText.toLowerCase())
        })
        : ''
    },
    staffs () {
      return this.groupById(this.events.map(event => {
        event.staff.color = event.backgroundColor
        return event.staff
      }))
    }
  },
  methods: {
    ...mapActions({
      goToDate: 'Calendar/goToDate',
      updateEvent: 'Calendar/updateEvent',
    }),
    onSaveEvent (e) {
      console.log('saveevent');
      this.event = e
    },
    groupById (staffs) {
      return staffs.sort().reduce((init, current) => {
        if (init.length === 0 || init[init.length - 1].id !== current.id) {
          init.push(current);
        }
        return init;
      }, []);
    },
    async getAllStaffs () {
      this.selected = this.parseListStaffs(this.staffs)
    },
    parseStaff (staff) {
      staff.fullName = staff.firstName + ' ' + staff.lastName
      return staff
    },
    parseListStaffs (staffs) {
      return staffs.map(staff => this.parseStaff(staff))
    },
    toggleEventFilter (selecteds) {
      this.staffs.map(staff => {
        this.hiddenEvents(staff.id)
      })
      selecteds.map(selected => {
        this.showEvents(selected.id)
      })
    },
    toggleAll (checked) {
      this.selected = checked ? this.filteredList.slice() : []

      if (checked) {
        this.selected.map(selected => {
          this.showEvents(selected.id)
        })
      }
      else {
        this.staffs.map(staff => {
          this.hiddenEvents(staff.id)
        })
      }
    },
    hiddenEvents (staffId) {
      let events = window.document.getElementsByClassName("event-staff-id-" + staffId)

      Array.from(events).forEach(el => {
        el.classList.add('hidden-event')
      })

      this.events.map(event => {
        if (event.classNames == "event-staff-id-" + staffId) {
          let e = this.cloneObject(event)
          e.classNames.push('hidden-event')
          this.updateEvent(e)
        }
      })

    },
    showEvents (staffId, isDefault = false) {
      if (isDefault) {
        this.events.map(event => {
          let e = this.cloneObject(event)
          let isHidden = event.classNames[3] === 'hidden-event'
          console.log(e.classNames, 'load');
          if (isHidden) {
            e.classNames = e.classNames.pop()

          }
          // e.classNames = "event-staffId-" + event.staff.id
          this.updateEvent(e)
        })
        return
      }

      let events = window.document.getElementsByClassName("event-staff-id-" + staffId)

      console.log(events, 'events classeds');
      Array.from(events).forEach(el => {
        el.classList.remove('hidden-event')
      })

      this.events.map(event => {
        let eventStaffId = event.classNames[0].split('-')[2]
        let isHidden = event.classNames[3] === 'hidden-event'

        if (eventStaffId === staffId && isHidden) {
          let e = this.cloneObject(event)
          e.classNames = e.classNames.pop()
          this.updateEvent(e)
        }

        // if (event.classNames == "event-staffId-" + staffId + ' hidden-event') {
        //   let e = this.cloneObject(event)
        //   e.classNames = "event-staffId-" + staffId
        //   this.updateEvent(e)
        // }
      })
    },
    onDateClickEvent (event) {
      this.onDateClick = event
    },
    showModal () {
      this.$bvModal.show('form-calendar-modal')
    },
    getDayEvents () {
      let dayEvents = this.events.filter(event => {
        let today = new Date().toISOString().split('T')[0]
        let day = new Date(event.start || event.date).toISOString().split('T')[0]

        return day === today
      })

      return dayEvents
    },
    getPeriodConsult (event) {
      if (event.allDay) return 'all day'

      let startDate = new Date(event.start)
      let endDate = new Date(event.end)

      let startTime = this.getTime(startDate);

      let endTime = this.getTime(endDate);

      return `${startTime} to ${endTime}`
    },
    getTime (dt) {
      var date = new Date(dt);
      var h = date.getHours();
      var m = date.getMinutes();
      h = this.checkTime(h);
      m = this.checkTime(m);

      return h + ':' + m
    },

    checkTime (i) {
      if (i < 10) {
        i = "0" + i;
      }
      return i;
    },
  }
}
</script>
<style scoped>
.doctor-list {
  display: flex;
  flex-direction: column;
}
</style>